import { filter, sum } from 'lodash';
import { Icon } from '@iconify/react';
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink, useParams } from 'react-router-dom';
// material
import { useSnackbar } from 'notistack';
import { useTheme, styled } from '@mui/material/styles';
import {
  Card,
  Table,
  Stack,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Grid,
  Box
} from '@mui/material';
import axios from '../../utils/axios';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import { FicheListHead, FicheListToolbar } from '../../components/_dashboard/fiche/list';
import { fDateTimeSuffix } from '../../utils/formatTime';
import { fCurrency } from '../../utils/formatNumber';
import useAuth from '../../hooks/useAuth';
// ----------------------------------------------------------------------

const TABLE_HEAD = [
  // { id: 'lotto', label: 'Option', align: 'left' },
  { id: 'boule', label: 'Pari', align: 'left' },
  { id: 'montant', label: 'Mise', align: 'center' },
  { id: 'gain', label: 'Gain', align: 'right' }
];

// ----------------------------------------------------------------------

const RowResultStyle = styled(TableRow)(({ theme }) => ({
  '& td': {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1)
  }
}));

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_fiche) => _fiche?.boule.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function Fiche() {
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const { isAuthenticated } = useAuth();
  const { refCode } = useParams();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [order, setOrder] = useState('asc');
  const [orderBy, setOrderBy] = useState('boule');

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };
  const [dataFiche, setDataFiche] = useState(null);
  const filteredBoules = applySortFilter(dataFiche?.boules, getComparator(order, orderBy), '');
  const isBouleNotFound = filteredBoules?.length === 0;

  const total = sum(filteredBoules?.map((item) => item.montantWin));

  const handleGo = async () => {
    if (!loading) {
      setLoading(true);
      try {
        const response = await axios.get(`/api/games/find-fiche-another?barcode=${refCode}`);
        setDataFiche(response.data);
      } catch (e) {
        enqueueSnackbar('Fiche introuvable!', { variant: 'error' });
      }
      setLoading(false);
    }
  };

  useEffect(() => {
    handleGo();
  }, []);

  return (
    <Page title={`Fiche: ${refCode} | RLP Center Lotto`}>
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <Stack alignItems="center">
          <br />
          <img alt="LOGO" src={dataFiche?.company_photo} style={{ height: '100px' }} />
          <br />
          <Typography paragraph variant="overline">
            {dataFiche?.company_name}
          </Typography>
          <Typography paragraph variant="overline">
            POS : {dataFiche?.pos}
          </Typography>
          <Typography paragraph variant="overline">
            Tirage : {dataFiche?.tirage_name}
          </Typography>
          {dataFiche?.is_win && (
            <Typography paragraph variant="overline" sx={{ color: dataFiche?.is_paid ? 'green' : 'red' }}>
              Fiche {dataFiche?.is_paid ? 'Pay' : 'Non pay'}
            </Typography>
          )}
          {dataFiche && (
            <Typography paragraph variant="overline">
              {fDateTimeSuffix(dataFiche?.created_on)}
            </Typography>
          )}
        </Stack>
        <Scrollbar>
          <TableContainer maxWidth={600}>
            <Table>
              <FicheListHead
                order={order}
                orderBy={orderBy}
                headLabel={TABLE_HEAD}
                rowCount={filteredBoules?.length}
                numSelected={0}
                onRequestSort={handleRequestSort}
                onSelectAllClick={() => null}
              />
              <TableBody>
                {filteredBoules?.map((item) => (
                  <TableRow>
                    {/* <TableCell align="left" sx={{ color: '#143fff' }}>
                        {item?.lotto}
                      </TableCell> */}
                    <TableCell align="left" sx={{ color: '#143fff' }} width={60}>
                      {item?.lotto_name} <br />
                      {item.lotto === 'MA'
                        ? `${item.boule?.substring(0, 2)}x${item.boule?.substring(2, 4)}`
                        : item.boule}{' '}
                      {item.lotto === 'L4' || item.lotto === 'L5' ? item.option : ''}
                    </TableCell>
                    <TableCell align="center" sx={{ color: '#143fff' }} width={60}>
                      {item?.montant}
                    </TableCell>
                    <TableCell align="right" sx={{ color: '#143fff' }} width={60}>
                      {item?.position}
                      <br />
                      {item?.montantWin}
                    </TableCell>
                  </TableRow>
                ))}
                {!isBouleNotFound && (
                  <RowResultStyle>
                    <TableCell colSpan={1} />
                    <TableCell align="center">
                      <Typography variant="subtitle2">
                        T.Mise: {fCurrency(parseFloat(dataFiche?.montant).toFixed(2))}
                      </Typography>
                    </TableCell>
                    <TableCell align="right">
                      <Typography variant="subtitle2">T.Gain: {fCurrency(parseFloat(total).toFixed(2))}</Typography>
                    </TableCell>
                  </RowResultStyle>
                )}
              </TableBody>
            </Table>
          </TableContainer>
        </Scrollbar>
        <br />
        <br />
        <Grid item xs={12} md={12}>
          <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <Button variant="contained" size="large" sx={{ px: 3 }} onClick={() => window.print()}>
              Imprimer
            </Button>
          </Box>
        </Grid>
        <br />
      </Container>
    </Page>
  );
}
